.App {
  text-align: center;
  margin: 0px;
  width: 100%;
  padding: 0px;
  -ms-flex-align: center;
      align-items: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 50px;
}

.App-header {
  position: -webkit-sticky;
  position: sticky;
  background-color: #222;
  background: -webkit-gradient(linear, left top, left bottom, from(#FF1744), to(#d50000));
  background: -webkit-linear-gradient(#FF1744, #d50000);
  background: -o-linear-gradient(#FF1744, #d50000);
  background: linear-gradient(#FF1744, #d50000); /* Standard syntax */
  width: 96%;
  margin: 0px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 2%;
  color: white;  
  
}

.App-title {
  font-size: 1.5em;
}

.ImageContainer {
  text-align: center;
  padding: 0px;
  margin: 0 auto;
  width: 100%;
}
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



