.image-item
{
    color: rgb(34, 34, 34);
    width: 100%;
    height: 150px;
    background-color: #fafafa;
    margin: 0 auto; 
    margin-bottom: 4px;
    padding: 0px;
    display: block;
    text-align: center;
}

.image-item-img-div
{
    display: inline-block;
    margin: 0 auto;
    vertical-align:top;
    height: 100%;
}
.image-item-img
{
    width: 100%;
    max-width: 150px;
    margin: 0 auto;
}

.image-item-form
{
    vertical-align:top;
    display: inline-block;
    height: 100%;
    width: 59%;
    text-align: center;
}

.image-item-form-field-title
{
    text-align: center;
    margin: 0 auto;
    margin-top: 4px;
    padding: 0px;
    width: 100%;
    left: 0;
}

.image-item-category
{
    width: 150px;
    text-align: center;
    border: none;
    background-color: #ffff00;
    margin-top: 10px;
    margin-right: 10px;
}

.image-item-info
{
    width: 100%;
    text-align: center;
    border: none;
    background-color: #ffff00;
    margin-top: 4px;
    margin-right: 1%;
}

.image-item-status 
{
    width: 50px;
}

.image-item-submit
{
    display: block;
    position: relative;
    right: 0px;
    width: 100px;
}

.image-item-label
{
    margin: 0 auto;
}


